/* Technicians.css */

/* Nastaven aknch tlatek v kart */
.card-actions {
    display: flex;
    justify-content: space-around; /* Rovnomrn rozloen */
    flex-wrap: wrap; /* Zalamovn tlatek na dal dky */
    gap: 1px; /* Men mezery mezi tlatky */
    align-items: center; /* Vertikln zarovnn na sted */
  }
  
  .card {
    min-width: 800px; /* Nastaven minimln ky karty */
    word-wrap: break-word; /* Zalamovn dlouhho textu */
  }
  